<template>
    <div class="block-price">
        <div class="block-price__table" :class="classes">
            <div class="block-price__item">
                <span class="block-price__table-title" v-if="$store.state.isLangGeo">{{ $store.state.text[1].geo }}</span>
                <span class="block-price__table-title" v-else>{{ $store.state.text[1].rus }}</span>
                <span class="block-price__table-title" v-if="$store.state.isLangGeo" v-html="$store.state.text[2].geo"></span>
                <span class="block-price__table-title" v-else v-html="$store.state.text[2].rus"></span>
            </div>
            <div class="block-price__item block-price__item-count" :class="`block-price__item--${index+1}`" v-for="(item, index) in price" :key="index">
                <span class="block-price__table-count">{{ item.quantity }}</span>
                <span class="block-price__table-count">{{ item.price }}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    sum: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      price: [
        {
          quantity: '1-3',
          price: '1000'
        },
        {
          quantity: '4-6',
          price: '900'
        },
        {
          quantity: '7-9',
          price: '800'
        },
        {
          quantity: '10+',
          price: '700'
        }

      ]
    }
  },
  computed: {
    classes () {
      return {
        '': this.sum === '',
        'block-price__table--1000': this.sum < 4 && this.sum > 0,
        'block-price__table--900': this.sum > 3 && this.sum < 7,
        'block-price__table--800': this.sum > 6 && this.sum < 10,
        'block-price__table--700': this.sum > 9
      }
    }
  }
}
</script>

