<template>
    <div id="app" class="main">
        <header class="main-header">
            <SectionHeader/>
        </header>
        <main class="main-body">
            <SectionMain/>
        </main>
        <footer class="main-footer">
            <SectionFooter/>
        </footer>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
import SectionHeader from '@/components/sections/header'
import SectionMain from '@/components/sections/main'
import SectionFooter from '@/components/sections/footer'

export default {
  components: {
    SectionHeader,
    SectionMain,
    SectionFooter
  },
  methods: {
    ...mapActions(['changeIsLangGeo'])
  },
  created () {
    const isRus = localStorage.getItem('lang') === 'rus'
    isRus ? this.changeIsLangGeo(false) : this.changeIsLangGeo(true)
  }
}
</script>

<style lang="scss">
    .main {
        display: flex;
        flex-direction: column;
        &-body {
            flex-grow: 1;
        }
    }
</style>
